import Loader from "@/components/Loader";

export default {
    name: "Contact",
    components: {
        Loader,
    },
    asyncData() {
        return {
            contactFormData: {
                naam: null,
                email: null,
                bericht: null,
            },
            sending: false,
            errors: {
                naam: "",
                email: "",
                bericht: "",
            },
        };
    },
    methods: {
        resetErrors: function (event) {
            const target = event.target.id;
            this.errors[target] = "";
        },
        sendMail: function () {
            if (!this.validForm()) {
                return;
            }

            this.sending = true;
            this.recaptcha();

            const url =
                "https://us-central1-dinnerwalks-7fc99.cloudfunctions.net/sendEmailCF";
            fetch(url, {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                },
                body: JSON.stringify({
                    naam: this.contactFormData.naam,
                    email: this.contactFormData.email,
                    bericht: this.contactFormData.bericht,
                }),
            })
                .then(() => {
                    this.$swal.fire({
                        icon: "success",
                        title: "Woehoe!",
                        text:
                            "Je bericht is verstuurd, we zullen zo spoedig mogelijk reageren.",
                    });
                    this.resetForm();
                    this.sending = false;
                })
                .catch(() => {
                    this.$swal.fire({
                        icon: "error",
                        title: "Oeps...",
                        text:
                            "Er is iets mis gegaan! Probeer het contactformulier nogmaals te versturen, de pagina te verversen of stuur ons een berichtje via sociale media :)",
                    });
                    this.sending = false;
                });
        },
        resetForm: function () {
            this.contactFormData = {
                naam: "",
                email: "",
                bericht: "",
            };
        },
        validForm: function () {
            let noErrors = true;
            for (const input in this.contactFormData) {
                if (!this.contactFormData[input]) {
                    this.errors[input] =
                        "Je hebt je " + input + " niet ingevuld!";
                    noErrors = false;
                }
            }
            if (
                !this.validEmail(this.contactFormData.email) &&
                this.errors.email == ""
            ) {
                this.errors.email = "Je e-mail is niet geldig!";
                noErrors = false;
            }
            return noErrors;
        },
        validEmail: function (email) {
            var re = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
            return re.test(email);
        },
        async recaptcha() {
            await this.$recaptchaLoaded();
        },
    },
};
